import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snUxfDemoNavigationStyles from './_sn-uxf-demo-navigation.scss';
import '@servicenow/now-heading';


const snUxfDemoNavigation = (state) => {
	const { label, linksetGroup } = state.properties;

	return (
		<section className="sn-uxf-demo-navigation">
      <now-heading
          className="heading"
          label={label}
          level="3"
          variant="header-primary"
          wont-wrap
      />
      <div className="linkset-wrap">
        {
          linksetGroup.map((linkset) => (
              <sn-canvas-linkset
                  heading={linkset.heading}
                  links={linkset.links}
                  browseLink={linkset.browseLink}
                  size={linkset.size}
                  variant={linkset.variant}
              />
          ))
        }
      </div>
		</section>
	);
};

createCustomElement('sn-uxf-demo-navigation', {
	properties: {
		label: { default: 'Find the info you need' },
    linksetGroup: { default: [] }
	},
	renderer: {
		type: snabbdom,
		view: snUxfDemoNavigation
	},
	styles: snUxfDemoNavigationStyles
});
