import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snCTIStyles from './_sn-cti.scss';
import '@servicenow/now-icon';
import { draggableBehavior } from '../../behaviors/draggable/draggableBehavior';

const {
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

const toggleVisibility = (opened) => {
  const hide = { 'display': 'none' };
  return !opened ? hide: {};
};

const snCti = (state, { updateProperties }) => {
	const {
		opened,
		containerProps,
		status,
		dockingEnabled
	} = state.properties;

	return (
      <div class={{
        'sn-cti': true,
        '-undocked': status === 'UN_DOCKED'
      }}
           style={{ ...containerProps, ...toggleVisibility(opened), 'background': '#fff' }}
      >
        <div className="sn-cti-header">
          <now-icon className="chevron-up-icon" icon="chevron-up-outline" size="lg"/>
          <div className="incoming-phone-details">
            <div className="incoming-phone">
              <now-icon className="incoming-phone-icon" icon="phone-outline" size="md"/>
              <span className="incoming-phone-label">Incoming Call</span>
            </div>
            <div className="phone-number">+1 (716) 867 - 5446</div>
          </div>
          {
            dockingEnabled && (status === 'DOCKED')
                ? <now-icon className="close-icon"
                            icon="clone-outline"
                            size="lg"
                            on-click={() => {
                              if (dockingEnabled && status === 'DOCKED') {
                                updateProperties({ status: 'UN_DOCKED' })
                              } else {
                                updateProperties({ status: 'DOCKED' })
                              }
                            }}
                />
                : <now-icon className="close-icon"
                            icon="close-outline"
                            size="lg"
                            on-click={() => {
                              if (dockingEnabled && status === 'DOCKED') {
                                updateProperties({ status: 'UN_DOCKED' })
                              } else {
                                updateProperties({ status: 'DOCKED' })
                              }
                            }}
                />
          }

        </div>
        <div className="sn-cti-phone">
          <button className="phone-actions">
            <now-icon icon="phone-outline" size="lg"/>
          </button>
          <button className="phone-actions">
            <now-icon style={{ "transform": "rotateY(180deg)" }} icon="phone-slash-outline" size="lg"/>
          </button>
        </div>
        <div className="sn-cti-footer">
          <now-icon icon="microphone-slash-outline" size="lg"/>
          <now-icon icon="circle-pause-outline" size="lg"/>
          <now-icon icon="grid-four-outline" size="lg"/>
        </div>

      </div>
	);
};

createCustomElement('sn-cti', {
	properties: {
		opened: { default: false, manageable: true },
		containerProps: { default: {} },
		dockingEnabled: { default: true },
		status: { default: 'DOCKED' }
	},
	renderer: {
		type: snabbdom,
		view: snCti
	},
	styles: snCTIStyles,
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: {
			effect({ state, dispatch }) {
				if (state.properties.opened) {
					dispatch('SN_CTI#TOGGLE_OPENED', { opened: true });
				}
			}
		}
	},
  behaviors: [draggableBehavior]
});
