import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snUxfDemoInfoStyles from './_sn-uxf-demo-info.scss';
import '@servicenow/now-heading';


const snUxfDemoInfo = (state) => {
	const { heading, content } = state.properties;

	return (
		<div className="sn-uxf-demo-info">
      <now-heading label={heading} level="1" variant="header-primary" />
      <div className="sn-uxf-demo-info-content">
        {JSON.stringify(content)}
      </div>
		</div>
	);
};

createCustomElement('sn-uxf-demo-info', {
	properties: {
	  heading: { default: '' },
		content: { default: {} }
	},
	renderer: {
		type: snabbdom,
		view: snUxfDemoInfo
	},
	styles: snUxfDemoInfoStyles
});
