import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snUxfDemoLogoutStyles from './_sn-uxf-demo-logout.scss';
import '@servicenow/now-heading';
import '@servicenow/now-image';
import '@servicenow/now-button';
import get from 'lodash/get';

import logoutImg from './assets/logout_page_img.png';

const AUTH_ROUTES = get(window, "ux_globals.routeConfiguration.authRoutes",{});
const LOGIN_ROUTE = get(AUTH_ROUTES, 'login', 'login');

const snUxfDemoLogout = (state) => {
	const { message } = state.properties;

	return (
      <div className="sn-uxf-demo-logout">
        <now-image className="graphic" src={logoutImg} alt="logout_image" width="279" height="296" />
        <now-heading className="message" label={message} level="1" variant="title-primary" />
        <now-button-bare
            label="Click to login"
            size="md"
            variant="primary"
            append-to-payload={{logInClicked: true}}
        />
      </div>
	);
};

createCustomElement('sn-uxf-demo-logout', {
	properties: {
		message: { default: 'You have successfully logged out of the system.' }
	},
	renderer: {
		type: snabbdom,
		view: snUxfDemoLogout
	},
	styles: snUxfDemoLogoutStyles,
  actionHandlers: {
	  ['NOW_BUTTON_BARE#CLICKED']: (coeffects) => {
      const {
        action: {
          payload: { logInClicked }
        },
        dispatch
      } = coeffects;
      logInClicked ? dispatch('NAV_ITEM_SELECTED', { route: LOGIN_ROUTE, fields: {} }): null;
    }
  }
});
