import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snUxfDemoCustom404Styles from './_sn-uxf-demo-custom404.scss';
import '@servicenow/now-heading';
import '@servicenow/now-image';
import '@servicenow/now-button';

import custom404Img from './assets/custom404_page_img.png';

const snUxfDemoCustom404 = (state) => {
    const { message } = state.properties;

    return (
        <div className="sn-uxf-demo-custom404">
            <now-image className="graphic" src={custom404Img} alt="custom404_image" width="279" height="296" />
            <now-heading className="message" label={message} level="1" variant="title-primary" />
        </div>
    );
};

createCustomElement('sn-uxf-demo-custom404', {
    properties: {
        message: { default: 'The page you are looking for could not be found.' }
    },
    renderer: {
        type: snabbdom,
        view: snUxfDemoCustom404
    },
    styles: snUxfDemoCustom404Styles,
});
