import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-heading';

import snabbdom from '@servicenow/ui-renderer-snabbdom';

const snUxfDemoSearch = (state) => {
	const { searchContextConfigId, searchTerm } = state.properties;
	return (
		<div>
			<now-heading label="Dummy search component" variant="header-primary"/>
			<now-heading label={`searchContextConfigId: ${searchContextConfigId}`} variant="header-secondary"/>
			<now-heading label={`searchTerm : ${searchTerm}`} variant="header-secondary"/>
		</div>
	);
};

createCustomElement('sn-uxf-demo-search', {
	renderer: {
		type: snabbdom,
		view: snUxfDemoSearch
	},
	properties: {
		searchContextConfigId: {
			defaultValue: ''
		},
		searchTerm: {
			defaultValue: ''
		}
	}
});