import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {
	READ_INTERACTION,
	READ_INTERACTION_SUCCESS,
	WATCH_INTERACTION_UPDATE,
	WATCH_INTERACTION_UNSUBSCRIBED,
	SUBSCRIBE_INTERACTION,
	INTERACTION_RECORD_UPDATED,
	CLOSE_MESSAGE,
	EXIT_CHAT_SUCCESS
} from './constants';
import get from 'lodash/get';
import includes from 'lodash/includes';

export const subscribeInteraction = createAmbSubscriptionEffect('/rw/default/interaction/:filter', {
	messageReceivedActionType: WATCH_INTERACTION_UPDATE
});

export const unsubscribeInteraction = createAmbSubscriptionEffect('/rw/default/interaction/:filter', {
	unsubscribeSucceededActionType: WATCH_INTERACTION_UNSUBSCRIBED
});

export function watchInteraction({action, dispatch, updateState}) {
	dispatch(READ_INTERACTION, {sys_id: action.payload.sys_id});
	dispatch(SUBSCRIBE_INTERACTION, {
		filter: btoa(`sys_id=${action.payload.sys_id}`),
		subscribe: true
	});
	updateState({
		interactionID: action.payload.sys_id
	});
}

export const readInteraction = createGraphQLEffect(`
query($sys_id: String!) {
	GlideRecord_Query {
		interaction(sys_id: $sys_id) {
			_results {
				active {
					value
				}
				type {
					value
				}
				sys_id {
					value
				}
				assigned_to {
					value
				}
			}
		}
	}
}
`, {
	variableList: ['sys_id'],
	successActionType: READ_INTERACTION_SUCCESS
});

export const exitChat = createGraphQLEffect(`
mutation($sys_id:String!) {
	GlideInteraction_Mutation {
		exit_chat(sys_id:$sys_id) {
			sys_id
			state
		}
	}
}
`, {
	variableList: ['sys_id'],
	successActionType: EXIT_CHAT_SUCCESS
});

export function readInteractionSuccess({action: {payload}, dispatch}) {
	const result = get(payload, 'data.GlideRecord_Query.interaction._results[0]');
	const interaction = {
		sys_id: result.sys_id.value,
		type: result.type.value,
		active: result.active.value,
		assigned_to: result.assigned_to.value
	};
	setInteractionTab(dispatch, interaction);
}

export function watchInteractionUpdate({action: {payload}, dispatch}) {
	const interaction = payload.data;
	if ( interaction.action !== 'change' ) {
		return;
	}

	if (includes(interaction.changes, 'active') ||  includes(interaction.changes, 'assigned_to')) {
		setInteractionTab(dispatch, interaction);
	}
}

function setInteractionTab(dispatch, interaction) {
	dispatch(INTERACTION_RECORD_UPDATED, {
		sys_id: interaction.sys_id,
		table: 'interaction',
		indicator: {
			type: interaction.type,
			status: interaction.active ? 'active' : 'inactive',
			message: CLOSE_MESSAGE
		}
	});
}