import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snUxfDemoBrowseStyles from './_sn-uxf-demo-browse.scss';
import '@servicenow/now-heading';


const snUxfDemoBrowse = (state) => {
	const { label, blurb, linksetGroup } = state.properties;

	return (
		<div className="sn-uxf-demo-browse">
      <aside className="browse--sidebar">
        <p>Container: Side Navigation</p>
      </aside>
      <main className="browse--content">
        <now-heading
            label={label}
            level="1"
            variant="header-primary"
            wont-wrap
        />
        <p className="browse--content-blurb">{blurb}</p>
        <div className="browse--linkset-wrap">
          {
            linksetGroup.map((linkset) => (
                <sn-canvas-linkset
                    heading={linkset.heading}
                    links={linkset.links}
                    browseLink={linkset.browseLink}
                    size={linkset.size}
                    variant={linkset.variant}
                />
            ))
          }
        </div>
      </main>
		</div>
	);
};

createCustomElement('sn-uxf-demo-browse', {
	properties: {
		label: { default: '' },
    blurb: { default: ''},
    linksetGroup: { default: [] }
	},
	renderer: {
		type: snabbdom,
		view: snUxfDemoBrowse
	},
	styles: snUxfDemoBrowseStyles
});
