import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import snUxfDemoHomeStyles from './_sn-uxf-demo-home.scss';
import '@servicenow/now-heading';


const snUxfDemoHome = (state) => {
	const { greetingText, userName } = state.properties;

	return (
		<div className="sn-uxf-demo-home">
      <section className="sn-uxf-demo-home-container">
        <div className="sn-uxf-demo-home-banner">
          <div className="sn-uxf-demo-home-overlay">
            <div className="sn-uxf-demo-home-content">
              <now-heading className="greet" label={`${greetingText} ${userName}.`} level="3" variant="header-primary" />
              <now-heading className="message" label="How can we help you?" level="1" variant="header-primary" />
            </div>
          </div>
        </div>
      </section>
		</div>
	);
};

createCustomElement('sn-uxf-demo-home', {
	properties: {
    greetingText: {default: 'Welcome'},
		userName: { default: null }
	},
	renderer: {
		type: snabbdom,
		view: snUxfDemoHome
	},
	styles: snUxfDemoHomeStyles
});
